html, body {
  height: 100%;
}

body {
  background: url(https://unsplash.it/1600/1200/?random&blur) no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  font-family: 'Montserrat', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

#overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0,0,0,0.5);
}

#text {
  position: fixed;
  bottom: 40px;
  left: 60px;
  color: rgba(255,255,255,1);
  font-size: 12px;
  letter-spacing: 1px;
  font-weight: 400;
}
